.section-hero {
  padding: 5rem 0 2.5rem;
  background-color: var(--section-color);
}
@media screen and (min-width: 63.99875rem) {
  .section-hero {
    padding: 10rem 0 4.0625rem;
  }
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .hero-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
}
.section-hero .left-block-desk {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .left-block-desk {
    align-items: flex-start;
    justify-content: flex-start;
    width: 50%;
    max-width: 30.5625rem;
  }
}
.section-hero .right-block-desk {
  width: 100%;
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .right-block-desk {
    width: 40%;
    max-width: 32.9375rem;
  }
}
.section-hero .title-wrapper {
  padding-bottom: 1.25rem;
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .title-wrapper {
    padding-bottom: 0;
    width: 100%;
    max-width: 30.5625rem;
  }
}
.section-hero .title-wrapper h1,
.section-hero .title-wrapper h2,
.section-hero .title-wrapper h3,
.section-hero .title-wrapper h4,
.section-hero .title-wrapper h5,
.section-hero .title-wrapper h6 {
  text-align: center;
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .title-wrapper h1,
  .section-hero .title-wrapper h2,
  .section-hero .title-wrapper h3,
  .section-hero .title-wrapper h4,
  .section-hero .title-wrapper h5,
  .section-hero .title-wrapper h6 {
    text-align: left;
  }
}
.section-hero .content-wrapper {
  margin-top: 1.5rem;
  color: var(--text-color);
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .content-wrapper {
    margin-top: 0;
    max-width: 24.5625rem;
  }
}
.section-hero .img-content {
  width: 100%;
  padding-top: 1rem;
}
.section-hero .img-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  position: relative;
}
.section-hero .img-wrapper .img {
  width: 100%;
}
.section-hero .img-wrapper .img picture {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.section-hero .img-wrapper .img img {
  max-width: 26.25rem;
}
.section-hero .btn-wrapper {
  margin-top: 1.5rem;
  display: flex;
  width: 100%;
  gap: 1.5rem;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .btn-wrapper {
    gap: 3.25rem;
    justify-content: flex-start;
  }
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .total-wrap.total-title-wrap {
    top: 4%;
    left: -32%;
    width: 17.6875rem;
  }
}
@media screen and (min-width: 77.49875rem) {
  .section-hero .total-wrap.total-title-wrap {
    left: -13%;
  }
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .total-wrap.total-subscr-wrap {
    bottom: 10%;
    left: -2%;
    width: 9.6875rem;
  }
}
@media screen and (min-width: 77.49875rem) {
  .section-hero .total-wrap.total-subscr-wrap {
    left: 8%;
  }
}
@media screen and (min-width: 47.99875rem) {
  .section-hero .total-wrap.total-rating-wrap {
    top: 21%;
    right: -8%;
    width: 9.4375rem;
  }
}
@media screen and (min-width: 77.49875rem) {
  .section-hero .total-wrap.total-rating-wrap {
    right: -4%;
  }
}